<template>
  <div class="login text-center ">
    <div class="bg-primary-gradient">
      <div class="logo">
        <v-img
            :src="require('../assets/logo-white.png')"
            contain
            height="176"
            class="mb-5"
        />
      </div>
      <div class="title-primary">
        <div class="title">Identificación</div>
      </div>

      <div style="padding: 0 3rem">
        <v-text-field
            v-model="email"
            label="Correo"
            filled
            append-icon="mdi-lock"
            background-color="white"
        ></v-text-field>
        <v-text-field
            v-model="password"
            label="Contraseña"
            type="password"
            filled
            append-icon="mdi-lock"
            background-color="white"
        ></v-text-field>
      </div>

      <div class="my-5">
        <v-btn width="240" :loading="isLoading" @click="login">Ingresar</v-btn>
      </div>
      <div>
        <v-btn width="240" @click="goToForgotPassword">Olvidé mi contraseña</v-btn>
      </div>
    </div>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
    <div v-if="hasError" style="position: absolute; bottom: 0; text-align: left; width: 100%; padding: 0.5rem">
      <v-alert v-model="hasError" type="error" dismissible width="100%" transition="scale-transition">
        Correo y/o contraseña incorrectos.
      </v-alert>
    </div>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'Login',
  data: () => ({
    isLoading: false,
    hasError: false,
    email: '',
    password: '',
    rules: {
      required: value => !!value || 'Campo Obligatorio.',
      counter: value => value.length <= 20 || 'Max 20 characters',
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'Correo Electronico Invalido.'
      },
    }
  }),
  created() {

  },
  methods: {
    login() {
      this.isLoading = true

      let data = {email: this.email, password: this.password}

      axios.post('auth/login', data).then((response) => {

        console.log(response)
        this.$store.dispatch('login', response.data)
        this.$router.push({name: 'Home'})
      }).catch(() => {
        this.hasError = true
      }).then(() => {
        this.isLoading = false
      })
    },
    goToForgotPassword() {
      this.$router.push({name: 'Password'})
    },
  }
}
</script>

<style lang="scss" scoped>
.login {
  color: white;

  .logo {
    padding-top: 10%;
  }

  .title-primary {
    padding: 2rem 0;
  }
}
</style>